.app__about {
    overflow: hidden;
}

.app__black {
    background: black;
}

.app__about_heading {
    display: flex;
    justify-content: center;
    align-items: center;
}

.section__padd {
    padding: 0rem 10% 0rem 10%;
}

.app__about_heading img {
    position: relative;
}

.app__about_heading h1 {
    position: absolute;
    font-size: 500%;
    color: #fad54f;
    top: 45%;
    text-shadow: 3px 2px 5px black;
}

.app__about_heading p {
    position: absolute;
}




.app__about_part2 {
    flex-direction: column;
}

.app__about_part2 h1 {
    text-align: center;
}

.app__about_part2_p {
    margin: 2rem 0;
    text-align: center;
}

.logo-image {
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    padding: 0 5rem 3rem 0;
}

.logo-image img {
    height: 146px;
    width: 146px;
}
.logo-image1 {
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    padding: 3rem 0 0 3rem ;
}

.logo-image1 img {
    height: 146px;
    width: 146px;
}




.app__about_part3 {
    background: black;
    flex-direction: column;

}

.app__about_part3_title{
    flex-direction: column;
}

.app__about_part3_title h1{
    text-align: center;
}

.customers_image img{
    height: 150px;
    width: 150px;
}

.app__about_part3_card {
    margin: 5rem 0rem 0rem 0rem;
    display: flex;
    flex-direction: row;
    width: 45%;
}
.card_container {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    
}

.customers_comments {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    padding: 0 2rem;
}

@media screen and (max-width: 550px) {
    .logo-image {
        padding: 1rem 2rem 2rem 0;
    }
    .logo-image img {
        height: 100px;
        width: 100px;
    }
    .logo-image1 {
        padding: 1rem 0rem 0rem 1rem;
    }
    .logo-image1 img {
        height: 100px;
        width: 100px;
    }
}
  

@media screen and (max-width: 450px) {
    .app__about_heading h1 {
        font-size: 300%;
    }

    .logo-image {
        display: none;
    }
    .app__about_part2 h1 {
        font-size: 2.5rem;
        line-height: 60px;
    }
    .app__about_part3_card {
        display: flex;
        align-items: center;
        justify-content: center;
    }
    .customers_comments {
        justify-content: center;
        align-items: flex-start;
    }
}
  
@media screen and (max-width: 850px) {
    .app__about_part3_card {
        width: 100%;
    }
    
}
  


