.app__navbar {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background: var(--color-black);
    padding: 0.5rem 2rem;
    padding-bottom: 0;
}

.app__navbar-logo {
    display: flex;
    justify-content: flex-start;
    align-items: center;
}

.app__navbar-logo img {
    width: 150px;
}

.app__navbar-links {
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: center;
    list-style: none;

}

.app__navbar-links li {
    margin: 0 1rem;
    cursor: pointer;
}

.app__navbar-links a {
    font-family: var(--font-base);
    font-size: 1.2rem;

}

.app__navbar-links li a::after {
    content: '';
    display: block;
    width: 0;
    height: 2px;
    background: var(--color-golden);
    transition: width .3s;
}

.app__navbar-links li a:hover::after {
    width: 100%;
}

.app__navbar-links li a:hover {
    transition: 0.5s ease;
    color: var(--color-golden);
    
}

.app__navbar-login {
    display: flex;
    justify-content: flex-end;
    align-items: center;

}

.app__navbar-login a {
    margin: 0 1rem;
    text-decoration: none;
    transition: 0.5s ease;
    font-family: var(--font-base);
    font-size: 1.2rem;
}

.app__navbar-login a::after {
    content: '';
    display: block;
    width: 0;
    height: 2px;
    background: var(--color-golden);
    transition: width .3s;
}

.app__navbar-login a:hover::after {
    width: 100%;
}

.app__navbar-login a:hover {
    color: var(--color-golden);
}

.app__navbar-login div {
    width: 1px;
    height: 30px;
    background-color: var(--color-golden);
}

.app__navbar-smallscreen {
    display: none;
}

.app__navbar-smallscreen_overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    background: var(--color-black);
    transition: 0.5s ease;
    flex-direction: column;
    z-index: 5;
}

.app__navbar-smallscreen_overlay .overlay__close {
    font-size: 27px;
    cursor: pointer;
    position: absolute;
    top: 20px;
    right: 20px;
}

.app__navbar-smallscreen_links {
    list-style: none;
}

.app__navbar-smallscreen_links li {
    margin: 2rem;
    cursor: pointer;
    font-size: 2rem;
    text-align: center;
    font-family: var(--font-base);
    color: var(--color-golden);
}

.app__navbar-smallscreen-login {
    margin: -1rem;
    cursor: pointer;
    font-size: 2rem;
    text-align: center;
    font-family: var(--font-base);
    color: var(--color-golden);
}

.gap{
    margin-top: 2rem;
}

.app__navbar-smallscreen_links li:after {
    content: '';
    display: block;
    margin: auto;
    height: 2px;
    width: 0px;
    background: transparent;
    transition: width .5s ease, background-color .5s ease;
  }

  .app__navbar-smallscreen_links li:hover:after {
    width: 100%;
    background: var(--color-golden);
  }

.app__navbar-smallscreen_links li:hover {
    color: var(--color-golden);
    transition: 0.5s ease;

}


@media screen and (max-width: 2000px) {
    .app__navbar-logo img {
        width: 210px;
    }
}

@media screen and (max-width: 1150px) {
    
    .app__navbar-links {
        display: none;
    }

    .app__navbar-smallscreen {
        display: flex;
    }
    
    .app__navbar-login {
        position: relative;
        right: -5%;
    
    }
    
}

@media screen and (max-width: 650px) {
    .app__navbar {
        padding: 1rem;
    }

    .app__navbar-login {
        display: none;
    }

    .app__navbar-logo img {
        width: 110px;
    }
}











