.app__laurels_awards {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-wrap: wrap;

  margin-top: 3rem;
}

.app__laurels_awards-card {
  flex: 1;
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  min-width: 230px;

  margin: 1rem;
}

.logo {
  padding: 4rem 0 0 4rem;
}
.laurels {
  display: flex;
}

.logo img {
  width: 146px;
  height: 146px;
}

.app__column {
  display: flex;
  flex-direction: column;
}

.this_Section__padding {
    padding-top: 0;
    padding-left: 8rem;
}

@media screen and (min-width: 1900px) {
  .app__laurels_awards-card {
    min-width: 390px;
  }
}

.app__laurels_awards-card img {
  width: 50px;
  height: 50px;
}

.app__laurels_awards-card_content {
  display: flex;
  flex-direction: column;
  margin-left: 1rem;
}

@media screen and (max-width: 450px) {
  .app__laurels_awards-card {
    min-width: 100%;
    margin: 1rem 0;
    margin-left: 5rem;
  }
  .logo {
    padding: 2rem 2rem 2rem 2rem !important;
    display: flex !important;
    justify-content: flex-end !important;

  }
  .this_Section__padding {
    flex: 1;
    padding-top: 0;
    padding-left: 1rem;
    display: flex;

  }
  .logo img {
    width: 100px;
    height: 100px;
  }
  .end {
    display: flex !important;
    justify-content: flex-end !important;
    align-items: center;
    position: relative;
    right: 0;
  }
}

@media screen and (max-width: 950px) {
  .app__laurels_awards-card {
    min-width: 100%;
    margin: 1rem 0;
  }
  .logo {
    padding: 3rem 0 3rem 3rem;
  }
  .app__laurels_awards-card {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .app__wrapper_info {
    display: flex;
    justify-content: center;
    align-items: flex-start;
  }
  .app__wrapper_img {
    margin-left: -5rem;
  }
}