.app__header {
  background-color: var(--color-black);
}

.app__header-h1 {
  font-family: var(--font-base);
  color: var(--color-golden);
  letter-spacing: 0.04em;
  text-transform: uppercase;
  line-height: 117px;
  font-size: 90px;
}

@media screen and (max-width: 450px) {
  .app__header-h1 {
    font-size: 60px;
    line-height: 75px;
  }
}

@media screen and (max-width: 950px) {
  .app__wrapper_img {
    margin-right: -5rem;
  }
}